@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.navbar{
    overflow: hidden;
    position: fixed;
    bottom: 0;
    width: 100%;
    padding-bottom: 3.5rem;
    z-index: 9999;
}
  
  
.navbar button{
    transform: scale(1.2);
}
  
.navbar button:focus {
    outline: none;
}
.fixtures img{
    height: 1.5rem;
}
  
.fixtures p{
    margin: auto;
    margin-left: 0rem;
    font-size: 0.7rem;
}
  
.fixtures .status-ft{
    font-size: 0.6rem;
    padding: 0.3rem;
    background-color:rgb(5, 68, 68);
    color: white;
    margin-top: 0.7rem;
}
  
.fixtures .status-on{
    font-size: 0.6rem;
    padding: 0.3rem;
    background-color: teal;
    color: white;
    margin-top: 0.7rem;
}
  
.fixtures .score{
    font-size: 1rem;
    margin-top: 0.6rem;
}

.fixtures .score .live-score{
    margin-left: 0.5rem;
    font-size: 1rem;
}
  
.fixtures .league-group{
    margin-bottom: 0.6rem;
}
  
.fixtures .league-title{
    padding: 0.2rem;
}
  
.fixtures .league-title img{
    margin: 0 0.3rem 0 0rem;
}
  
.fixtures .league-title p{
    font-weight: bold;
}

.news {
}

.news .card {
  width: 100%;
  margin-top: 1rem;
  box-shadow: 0px 0px 3px 0px rgba(0, 128, 128, 0.5);
}

.news .articles:last-child {
  margin-bottom: 4rem;
}

.news .appbar {
  background-color: blue;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.news button {
  background-color: teal;
}

.news .appbar button:focus {
  outline: none;
}

.fixture .appbar {
  background-color: #3a3a3a;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.fixture img {
  height: 4rem;
}

.fixture .score {
  text-align: center;
  margin: 0px;
  margin-top: 5rem;
  border-bottom: 2px solid teal;
}

.fixture .score .home-team {
  margin-left: 1rem;
}

.fixture .score .away-team {
  margin-right: 1rem;
}

.fixture .score .live-score {
  font-size: 1.5rem;
  margin-top: 0.7rem;
  font-weight: bold;
}

.fixture-tab .selected {
  border-bottom: 2px solid teal;
  padding-bottom: 0.1rem;
}

.fixture .stats {
  margin-top: 2rem;
  text-align: center;
}

.stats .col {
  margin-bottom: 1rem;
}

.fixture .stats .badge {
  font-size: 1rem;
  color: black;
}

.lineup {
  margin-top: 0.8rem;
  margin-bottom: 3rem;
}

.lineup h2 {
  font-size: 1rem;
  font-weight: bold;
}

.lineup p {
  margin: 0rem;
  padding: 0.3rem;
  font-size: 0.8rem;
  border: 1px solid rgba(184, 184, 184, 0.308);
  border-radius: 0.5rem;
  margin-bottom: 0.1rem;
}

.lineup .formation {
  color: rgb(131, 131, 131);
  position: absolute;
  left: 5%;
}

.lineup .manager {
  color: rgb(131, 131, 131);
  position: absolute;
  right: 5%;
  top: 1%;
  font-size: 0.8rem;
}

.lineup .team-players {
  margin-bottom: 2rem;
}

.lineup .player-number {
  position: absolute;
  right: 10%;
}

.lineup .player-name {
  margin-left: 0.2rem;
}

.facts {
  margin-top: 1.4rem;
  margin-bottom: 5rem;
}

.facts img {
  height: 1.5rem;
}

.facts .footer p {
  font-size: 0.8rem;
  margin-top: 0.3rem;
  margin-left: 0.3rem;
  font-weight: bold;
}

.facts .events p {
  font-size: 0.9rem;
  margin-top: 0.8rem;
}

.facts .events .player {
  font-weight: bold;
}

.facts .events .away-team .player {
  text-align: right;
  padding-right: 0;
}

.events .goal {
  height: 3rem;
}

.events .card {
  margin-top: 0.7rem;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.day-tab {
  border-bottom: 2px solid teal;
}

.home .appbar {
  background-color: #3a3a3a;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.home .clock {
  position: fixed;
  right: 3rem;
}

.loading-div {
  position: absolute;
  top: 40%;
  left: 45%;
}

@keyframes blinking {
  0% {
    background-color: teal;
  }
  50% {
    background-color: rgb(3, 184, 184);
  }

  100% {
    background-color: teal;
  }
}

.blink {
  animation: blinking 3s infinite;
}

a {
  color: black;
}

