.fixture .appbar {
  background-color: #3a3a3a;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.fixture img {
  height: 4rem;
}

.fixture .score {
  text-align: center;
  margin: 0px;
  margin-top: 5rem;
  border-bottom: 2px solid teal;
}

.fixture .score .home-team {
  margin-left: 1rem;
}

.fixture .score .away-team {
  margin-right: 1rem;
}

.fixture .score .live-score {
  font-size: 1.5rem;
  margin-top: 0.7rem;
  font-weight: bold;
}

.fixture-tab .selected {
  border-bottom: 2px solid teal;
  padding-bottom: 0.1rem;
}

.fixture .stats {
  margin-top: 2rem;
  text-align: center;
}

.stats .col {
  margin-bottom: 1rem;
}

.fixture .stats .badge {
  font-size: 1rem;
  color: black;
}

.lineup {
  margin-top: 0.8rem;
  margin-bottom: 3rem;
}

.lineup h2 {
  font-size: 1rem;
  font-weight: bold;
}

.lineup p {
  margin: 0rem;
  padding: 0.3rem;
  font-size: 0.8rem;
  border: 1px solid rgba(184, 184, 184, 0.308);
  border-radius: 0.5rem;
  margin-bottom: 0.1rem;
}

.lineup .formation {
  color: rgb(131, 131, 131);
  position: absolute;
  left: 5%;
}

.lineup .manager {
  color: rgb(131, 131, 131);
  position: absolute;
  right: 5%;
  top: 1%;
  font-size: 0.8rem;
}

.lineup .team-players {
  margin-bottom: 2rem;
}

.lineup .player-number {
  position: absolute;
  right: 10%;
}

.lineup .player-name {
  margin-left: 0.2rem;
}

.facts {
  margin-top: 1.4rem;
  margin-bottom: 5rem;
}

.facts img {
  height: 1.5rem;
}

.facts .footer p {
  font-size: 0.8rem;
  margin-top: 0.3rem;
  margin-left: 0.3rem;
  font-weight: bold;
}

.facts .events p {
  font-size: 0.9rem;
  margin-top: 0.8rem;
}

.facts .events .player {
  font-weight: bold;
}

.facts .events .away-team .player {
  text-align: right;
  padding-right: 0;
}

.events .goal {
  height: 3rem;
}

.events .card {
  margin-top: 0.7rem;
}
